<template>
    <div class="row justify-content-center">
        <div class="col-md-auto offset-md-auto col-11">
            <table class="table table-light rounded rounded-4 overflow-hidden" v-if="isMobile">
                <thead>
                    <tr>
                        <th class="p-0">
                            <button class="btn btn-light w-100" id="filterBtn1" type="button" data-bs-toggle="collapse" data-bs-target="#collapseLevel,#collapseStar" aria-expanded="false" aria-controls="collapseType">
                                <i class="bi bi-funnel-fill"></i>
                            </button>
                            <button class="btn btn-light w-100" id="filterBtn2" type="button" data-bs-toggle="collapse" data-bs-target="#collapseType,#collapseRarity,#collapseSet" aria-expanded="false" aria-controls="collapseType" style="display:none;">
                                <i class="bi bi-funnel-fill"></i>
                            </button>
                            <button class="btn btn-light w-100" id="filterBtn3" type="button" data-bs-toggle="collapse" data-bs-target="#collapseLevel,#collapseStar,#collapseType,#collapseRarity,#collapseSet" aria-expanded="false" aria-controls="collapseType" style="display:none;">
                                <i class="bi bi-funnel-fill"></i>
                            </button>
                        </th>
                    </tr>
                    <tr>
                        <td class="p-0">
                            <div class="collapse" id="collapseLevel">
                                <div class="input-group">
                                    <span class="input-group-text">{{ text.level }}:</span>
                                    <input type="number" aria-label="level" class="form-control" 
                                    style="background: transparent; font-weight: bold;" 
                                    min="1" v-model="level">
                                    <!-- <button class="btn px-3 fs-4" type="button">
                                        <i class="bi bi-sort-down"></i>
                                    </button> -->
                                    <button class="btn px-3 fs-4" type="button" @click="clearFilter">
                                        <i class="bi bi-arrow-clockwise"></i>
                                    </button>
                                </div>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td class="p-0">
                            <div class="collapse" id="collapseStar">
                                <div class="input-group" role="group">
                                    <span class="input-group-text"><i class="bi bi-star-fill"></i></span>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 0 }" @click="star = 0">0</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 1 }" @click="star = 1">1</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 2 }" @click="star = 2">2</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 3 }" @click="star = 3">3</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 4 }" @click="star = 4">4</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 5 }" @click="star = 5">5</button>
                                </div>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td class="p-0">
                            <div class="collapse" id="collapseType">
                                <select class="form-select" aria-label="EquipType" v-model="equipType">
                                    <option value="All">All</option>
                                    <option value="Weapon" selected>{{ text.weapon }}</option>
                                    <option value="Armor">{{ text.armor }}</option>
                                    <option value="Accessory">{{ text.accessory }}</option>
                                </select>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td class="p-0">
                            <div class="collapse" id="collapseRarity">
                                <select class="form-select" aria-label="EquipRarity" v-model="equipRarity">
                                    <option value="0">All</option>
                                    <option value="6">{{ text.mythic }}</option>
                                    <option value="5" selected>{{ text.legendary }}</option>
                                    <option value="4">{{ text.epic }}</option>
                                    <option value="3">{{ text.rare }}</option>
                                </select>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td class="p-0">
                            <div class="collapse" id="collapseSet">
                                <select class="form-select" aria-label="EquipSet" v-model="equipSet">
                                    <option value="All">All</option>
                                    <option :value="item[0]" v-for="(item) in setBonus" :key="item">
                                        {{ item[language] }}
                                    </option>
                                </select>
                            </div>
                        </td>
                    </tr>
                    <!-- <tr>
                        <td class="p-0">
                            <div class="collapse" id="collapseSearch">
                                <div class="input-group">
                                    <input type="text" aria-label="search" class="form-control" placeholder="Search" 
                                    style="background: transparent; font-weight: bold;"
                                    v-model="equipSearch">
                                </div>
                            </div>
                        </td>
                    </tr> -->
                </thead>
            </table>
            <table class="table table-light table-hover rounded rounded-4 overflow-hidden">
                <template v-if="isMobile">
                    <tbody>
                        <template v-for="item in EquipmentsFilter" :key="item">
                            <tr :id="item.imgName">
                                <th colspan="8" class="align-middle">
                                    <img :src="`img/EquipmentItem/${item.imgName}.png`" 
                                    :alt="item.imgName">
                                    <br>
                                    <span class="mt-2">{{ item.name }}</span>
                                </th>
                                <td colspan="16" class="align-middle" v-if="item.type == 'Weapon'">
                                    <h5 class="d-flex justify-content-end">{{ text.atk }}</h5>
                                    <h3 class="d-flex justify-content-end">{{ item.finalATK }}</h3>
                                    <p class="lh-sm">
                                        <span class="float-end" v-if="item.stat1">
                                            +{{ item.stat1.text }}&nbsp;
                                            {{ item.stat1.value[star] }}
                                            <span v-if="item.stat1.percent">%</span>
                                        </span>
                                        <br>
                                        <span class="float-end" v-if="item.stat2">
                                            +{{ item.stat2.text }}&nbsp;
                                            {{ item.stat2.value[star] }}
                                            <span v-if="item.stat2.percent">%</span>
                                        </span>
                                        <br>
                                        <span class="float-end" v-if="item.stat3">
                                            +{{ item.stat3.text }}&nbsp;
                                            {{ item.stat3.value[star] }}
                                            <span v-if="item.stat3.percent">%</span>
                                        </span>
                                    </p>
                                </td>
                                <td colspan="16" class="align-middle" v-else>
                                    <p class="lh-sm">
                                        <span class="float-end" v-if="item.primaryStat1">
                                            +{{ item.primaryStat1 }}&nbsp;
                                            {{ item.finalValue1 }}
                                            <span v-if="!item.primaryStat1isWhite">%</span>
                                        </span>
                                        <br>
                                        <span class="float-end" v-if="item.primaryStat2">
                                            +{{ item.primaryStat2 }}&nbsp;
                                            {{ item.finalValue2 }}
                                            <span v-if="!item.primaryStat2isWhite">%</span>
                                        </span>
                                    </p>
                                </td>
                            </tr>
                            <template v-if="item.SetBonusName">
                                <tr>
                                    <td colspan="24" class="p-0">
                                        <button class="btn w-100 p-0 setBtn" type="button" data-bs-toggle="collapse" :data-bs-target="'#' + item.fileName" aria-expanded="false" :aria-controls="item.fileName">
                                            <i class="bi bi-chevron-compact-down"></i>
                                            <i class="bi bi-chevron-compact-up" style="display:none;"></i>
                                        </button>
                                    </td>
                                </tr>
                                <tr>
                                    <td colspan="24" class="pb-0">
                                        <div class="collapse" :id="item.fileName">
                                            <template v-if="item.SetBonusName">
                                                <p class="lh-sm" v-for="(Description, index) in item.SetBonusDescription" :key="Description">
                                                    <span v-if="item.SetBonusDescription.length > 1">{{ item.SetBonusName }}: {{ index + 2 }} {{ text.PieceBonus }}</span>
                                                    <span v-else>{{ item.SetBonusName }}: {{ index + 1 }} {{ text.PieceBonus }}</span>
                                                    <br>
                                                    <span>{{ Description.Languages[language] }}</span>
                                                </p>
                                            </template>
                                        </div>
                                    </td>
                                </tr>
                            </template>
                        </template>
                    </tbody>
                </template>
                <template v-else>
                    <thead>
                        <tr>
                            <th colspan="5">
                                <select class="form-select" aria-label="EquipType" v-model="equipType">
                                    <option value="All">All</option>
                                    <option value="Weapon" selected>{{ text.weapon }}</option>
                                    <option value="Armor">{{ text.armor }}</option>
                                    <option value="Accessory">{{ text.accessory }}</option>
                                </select>
                            </th>
                            <th colspan="5">
                                <select class="form-select" aria-label="EquipRarity" v-model="equipRarity">
                                    <option value="0">All</option>
                                    <option value="6">{{ text.mythic }}</option>
                                    <option value="5" selected>{{ text.legendary }}</option>
                                    <option value="4">{{ text.epic }}</option>
                                    <option value="3">{{ text.rare }}</option>
                                </select>
                            </th>
                            <th colspan="6">
                                <select class="form-select" aria-label="EquipSet" v-model="equipSet">
                                    <option value="All">All</option>
                                    <option :value="item[0]" v-for="(item) in setBonus" :key="item">
                                        {{ item[language] }}
                                    </option>
                                </select>
                            </th>
                            <!-- <th colspan="6">
                                <div class="input-group">
                                    <input type="text" aria-label="search" class="form-control" placeholder="Search" 
                                    style="background: transparent; font-weight: bold;"
                                    v-model="equipSearch">
                                </div>
                            </th> -->
                            <th colspan="4">
                                <div class="input-group">
                                    <span class="input-group-text">{{ text.level }}:</span>
                                    <input type="number" aria-label="level" class="form-control" 
                                    style="background: transparent; font-weight: bold;" 
                                    min="1" v-model="level">
                                </div>
                            </th>
                            <th colspan="3">
                                <!-- <button class="btn w-100 p-0 fs-4" type="button">
                                    <i class="bi bi-sort-down"></i>
                                </button> -->
                                <button class="btn w-100 p-0 fs-4" type="button" @click="clearFilter">
                                    <i class="bi bi-arrow-clockwise"></i>
                                </button>
                            </th>
                            <th colspan="7">
                                <div class="input-group d-flex justify-content-end" role="group">
                                    <span class="input-group-text"><i class="bi bi-star-fill"></i></span>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 0 }" @click="star = 0">0</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 1 }" @click="star = 1">1</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 2 }" @click="star = 2">2</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 3 }" @click="star = 3">3</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 4 }" @click="star = 4">4</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 5 }" @click="star = 5">5</button>
                                </div>
                            </th>
                        </tr>
                        <tr>
                            <th colspan="4">Name</th>
                            <th colspan="4"><span class="float-end">Stat</span></th>
                            <th colspan="8">Set Bonus</th>
                            <th colspan="14">Description</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="item in EquipmentsFilter" :key="item" :id="item.imgName">
                            <th scope="row" colspan="4" class="align-middle" style="width:20%;">
                                <img :src="`img/EquipmentItem/${item.imgName}.png`" 
                                :alt="item.imgName">
                                <br>
                                <span class="mt-2">{{ item.name }}</span>
                            </th>
                            <td colspan="4" class="align-middle" v-if="item.type == 'Weapon'">
                                <h5 class="d-flex justify-content-end">{{ text.atk }}</h5>
                                <h3 class="d-flex justify-content-end">{{ item.finalATK }}</h3>
                                <p class="lh-sm">
                                    <span class="float-end" v-if="item.stat1">
                                        +{{ item.stat1.text }}&nbsp;
                                        {{ item.stat1.value[star] }}
                                        <span v-if="item.stat1.percent">%</span>
                                    </span>
                                    <br>
                                    <span class="float-end" v-if="item.stat2">
                                        +{{ item.stat2.text }}&nbsp;
                                        {{ item.stat2.value[star] }}
                                        <span v-if="item.stat2.percent">%</span>
                                    </span>
                                    <br>
                                    <span class="float-end" v-if="item.stat3">
                                        +{{ item.stat3.text }}&nbsp;
                                        {{ item.stat3.value[star] }}
                                        <span v-if="item.stat3.percent">%</span>
                                    </span>
                                </p>
                            </td>
                            <td colspan="4" class="align-middle" v-else>
                                <p class="lh-sm">
                                    <span class="float-end" v-if="item.primaryStat1">
                                        +{{ item.primaryStat1 }}&nbsp;
                                        {{ item.finalValue1 }}
                                        <span v-if="!item.primaryStat1isWhite">%</span>
                                    </span>
                                    <br>
                                    <span class="float-end" v-if="item.primaryStat2">
                                        +{{ item.primaryStat2 }}&nbsp;
                                        {{ item.finalValue2 }}
                                        <span v-if="!item.primaryStat2isWhite">%</span>
                                    </span>
                                </p>
                            </td>
                            <td colspan="8" class="align-middle">
                                <template v-if="item.SetBonusName">
                                    <p class="lh-sm" v-for="(Description, index) in item.SetBonusDescription" :key="Description">
                                        <span v-if="item.SetBonusDescription.length > 1">{{ item.SetBonusName }}: {{ index + 2 }} {{ text.PieceBonus }}</span>
                                        <span v-else>{{ item.SetBonusName }}: {{ index + 1 }} {{ text.PieceBonus }}</span>
                                        <br>
                                        <span>{{ Description.Languages[language] }}</span>
                                    </p>
                                </template>
                            </td>
                            <td colspan="14" class="align-middle">
                                <p>{{ item.description }}</p>
                            </td>
                        </tr>
                    </tbody>
                </template>
                
            </table>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, watch } from 'vue'
import { computed } from '@vue/runtime-core'
import { useMobile } from "@/store/Mobile"
import { useWeapon } from '@/store/Weapon'
import { useLanguage } from '@/store/Language'
import { storeToRefs } from 'pinia'

export default {
    name: 'WeaponData',
    setup() {
        const { language, text } = storeToRefs(useLanguage())

        useLanguage().changeLanguage()
        useWeapon().changeLanguage(language.value)
        useWeapon().setLevel()

        const { isMobile } = storeToRefs(useMobile())
        const {
            Equipments,
            setBonus,
            equipType,
            equipName,
            equipSearch,
            equipRarity,
            equipSet,
            level,
            star,
        } = storeToRefs(useWeapon())

        console.log(equipSet.value)
        console.log(language.value)

        const clearFilter = () => {
            equipType.value = "All"
            equipRarity.value = 0
            equipSet.value = "All"
            equipSearch.value = ""
        }

        const EquipmentsFilter = computed(() => {
            // return Equipments.value
          return Equipments.value
            .filter((item) => {
                if (item.setName == "") {
                    return ((equipType.value === item.type) || (equipType.value === "All"))
                    && ((equipRarity.value == item.rarity) || (equipRarity.value == 0))
                    && ((equipSet.value.toLowerCase() == item.setName.toLowerCase()) || (equipSet.value.replace(" ", "").replace("'", "").toLowerCase() == item.setName.toLowerCase()) 
                    || (equipSet.value == "All"))
                    && ((item.name.toLowerCase().includes(equipSearch.value.toLowerCase()))
                    || (equipSearch.value === ""))
                }else{
                    return ((equipType.value === item.type) || (equipType.value === "All"))
                    && ((equipRarity.value == item.rarity) || (equipRarity.value == 0))
                    && ((equipSet.value.toLowerCase() == item.setName.toLowerCase()) || (equipSet.value.replace(" ", "").replace("'", "").toLowerCase() == item.setName.toLowerCase()) 
                    || (equipSet.value == "All"))
                    && (item.imgName.toLowerCase().includes(equipSearch.value.toLowerCase()))
                    // && ((item.name.toLowerCase().includes(equipSearch.value.toLowerCase()))
                    // || (item.setName.toLowerCase().includes(equipSearch.value.toLowerCase()))
                    // || (item.SetBonusName.toLowerCase().includes(equipSearch.value.toLowerCase()))
                    // || (item.SetBonusDescription[0].Languages[language.value].toLowerCase().includes(equipSearch.value.toLowerCase()))
                    // || (equipSearch.value === ""))
                }
                
            })
            .sort((a, b) =>{ return (a.type > b.type) ? -1 : 1})
            .sort((a, b) =>{ return b.rarity - a.rarity})
        })

        watch(level, (nV) => {
            useWeapon().setLevel()
            console.log(EquipmentsFilter)
        })
        
        $(document).ready(function() {
            $(this).on("click", ".setBtn", function() {
                $(this).children().toggle();
            });
            $(this).on("click", "#filterBtn1", function() {
                $(this).toggle();
                $(this).siblings("#filterBtn2").toggle();
            });
            $(this).on("click", "#filterBtn2", function() {
                $(this).toggle();
                $(this).siblings("#filterBtn3").toggle();
            });
            $(this).on("click", "#filterBtn3", function() {
                $(this).toggle();
                $(this).siblings("#filterBtn1").toggle();
            });
        });

        return {
            isMobile,
            language,
            text,
            Equipments,
            setBonus,
            equipType,
            equipName,
            equipSearch,
            equipRarity,
            equipSet,
            level,
            star,
            EquipmentsFilter,
            clearFilter,
        }
    }
}
</script>

<style scoped>
table {
    table-layout:fixed;
    text-align: center;
}
img {
    width: 100%;
    opacity: 1;
}
@media screen and (max-width: 768px) {
}
</style>
